import React, { useState, useEffect } from "react";
import { Trans, useTranslation } from "gatsby-plugin-react-i18next";
import { FiMenu, FiX } from "react-icons/fi";
import { FaArrowUp } from "react-icons/fa";
import { useLocation } from "@reach/router";

interface NavProps {
  data?: {
    menu: string[];
  };
}

const Navigation: React.FC<NavProps> = ({ data }) => {
  const { t } = useTranslation();
  const { pathname } = useLocation();
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [showBackToTop, setShowBackToTop] = useState(false);

  if (!data || !data.menu) {
    return null;
  }

  const currentPath = pathname.replace(/^\/(en|fr)\//, "/").replace(/\/$/, "");

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 100) {
        setShowBackToTop(true);
      } else {
        setShowBackToTop(false);
      }
    };
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <nav className={styles.container}>
      <button onClick={toggleMenu} className={styles.burgerButton}>
        {isMenuOpen ? <FiX className="text-white fixed right-4 z-[70] top-4" size={24} /> : <FiMenu className="text-white" size={24} />}
      </button>

      <ul className={`${styles.buttonsWrapper} ${isMenuOpen ? styles.menuOpen : styles.menuClosed}`}>
        {data.menu.map((item: string, index: number) => (
          <li key={index} className={styles.buttonsContainer}>
            <button
              onClick={() => document.getElementById(item)?.scrollIntoView({ behavior: "smooth" })}
              className={styles.buttons}>
              <Trans i18nKey={`menu.${item}`}>
                {t(`menu.${item}`)}
              </Trans>
            </button>
          </li>
        ))}
      </ul>

      {showBackToTop && (
        <button onClick={scrollToTop} className={styles.backToTopButton}>
          <FaArrowUp size={20} />
        </button>
      )}
    </nav>
  );
};

export default Navigation;

const styles = {
  container: "inline-block justify-between items-center bg-primary px-4 py-2 rounded-none sm:rounded-full relative left-0 sm:left-auto z-50 w-full sm:w-auto",
  burgerButton: "md:hidden block text-primary focus:outline-none",
  buttonsWrapper: "flex flex-col md:flex-row justify-evenly items-center w-[920px] md:w-auto fixed sm:relative left-0 top-0 py-8 sm:py-0 gap-4",
  buttonsContainer: "my-4 sm:my-0",
  buttons: "bg-orange rounded-full text-white px-4 py-0 hover:underline hover:text-secondary",
  menuOpen: "block md:flex",
  menuClosed: "hidden md:flex",
  backToTopButton: "fixed bottom-4 right-4 bg-secondary text-white p-2 rounded-full shadow-lg z-50",
};
